.Nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, .25);
    width: 95%;
    height: 80px;
    border-radius: 13px;
}


.navbar-logo {
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}


.nav-menu{
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 1px;
    list-style: none;
    align-items: center;
}

.nav-links{
    text-decoration: none;
    color:#222;
    font-size:1.2rem;
    font-weight: 600;
    padding-top: 0px;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i{
    padding-right: 10px;
}

.nav-links:hover{
    background-color: rgb(216, 112, 180);
    color: #fff;
    border-radius:2px;
    transition: all 0.2s ease-in-out;

}
.nav-links-mobiles{
    display: none;
}

button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: rgb(112, 116, 216);
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

button:hover{
    background-color: black;
    color: #fff;
}

